* {
  background: #1B2845;
  color: #65AFFF;
}

#App {
  display:flex;
  justify-content: center;
  align-items: center;
  height:90vh;
  flex-direction: column;
}

#App .input {
  background:  #274060;
  color:#B4C5E4;
  font-size: 20px;
  transition: 0.2s;
}

#App .input:hover {
  background: #1B2845;
  color:#B4C5E4;
}


@media (max-width: 768px) {
  #App {
    text-align: center;
    zoom: 90%;
    height: 110vh;
  }

  #App .input, #App #country {
    max-width: 80vw; 
  }
}

button {
  width: 100px;
  height: 25px;
  border-radius: 5px;
  transition: 0.1s;
}

button:hover {
  background-color: #B4C5E4;
  color: #1B2845;
}

button:active {
  background-color: #1b2845;
  color: #b4c5e4
}